<template>
	<div class="shopSite_box">
		<header>
			<Haednav head_left="" head_title='编辑地址' head_right="">
				<router-link to="/site" slot="left" tag="span">
					<van-icon name="arrow-left" />
				</router-link>
				<div @click="showFlag" slot="contact">
					<span v-show="flag">完成</span>
					<span v-show="start">编辑</span>
				</div>
			</Haednav>
		</header>
		<div class="shopSite_cont">
			<ul>
				<li @click="routAffirm(item)" v-for="(item,index) in siteName" :key="index">
					<div>
						<p>{{item.name}}</p>
						<p>{{item.site}}</p>
					</div>
					<p v-if="flag" @click="delSite(index)">x</p>
				</li>
			</ul>
			<router-link to="/newSearch" tag="div" class="shopSite_newSite">
				<p>新增地址</p>
				<p>
					<span class="el-icon-arrow-right"></span>
				</p>
			</router-link>
		</div>
	</div>
</template>

<script>
	import Haednav from '../About/headNav'
	import {mapMutations,mapState} from 'vuex'
	export default {
		name: "shopSite",
		data(){
			return{
				flag:false,
				start:true,
				obj:{}
			}
		},
		methods:{
			//调用vuex中的存储
			...mapMutations(['seTst']),
			showFlag(){
				if(this.start){
					this.start=false
					this.flag=true
				}else{
					this.start=true
					this.flag=false
				}
			},
			//删除添加列表
			delSite(index){
				this.$store.commit('delSit',index)
			},
			//添加本地存储为订单也渲染
			routAffirm(item){
				this.$router.push({path:'/affirm'})
				this.$store.commit('routAff',item)
			}
		},
		mounted(){
			this.seTst()
			if(this.siteName.length<=0){
				this.start=true
			}
		},
		computed:{
			...mapState(['siteName'])
		},
		components:{
			Haednav,
		},
	}
</script>
